.product-page {
    padding: 0 20px;
    max-width: 800px;
    box-sizing: border-box;
}

.product-title {
    font-size: 1.9rem;
}

.product-info h1 {
    text-align: center;
    font-size: 1.5rem;
    margin: 0 0 1rem 0;

}

.product-page a {
    padding: 0 10px;
    text-decoration: none;
    color: inherit;
}

.product-main-image {
    width: 100%;
    min-height: 250px;
    background: #f7f7f7;
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 0.75rem;
}

.product-main-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    pointer-events: none;
}

.product-carousel ul {
    display: flex;
    gap: 0.5rem;
    padding: 1.5rem 0;
    list-style: none;
    overflow-x: auto;
}

.product-carousel li {
    flex: 0 0 auto;
}

.product-carousel button {
    width: 80px;
    height: 80px;
    padding: 2px;
    border-radius: 8px;
    background: #fff;
    border: 1px solid #ccc;
    cursor: pointer;
    position: relative;
    z-index: 10;
}

.product-carousel img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.product-colors ul {
    display: flex;
    gap: 0.5rem;
    list-style: none;
    margin: 1.6rem 0;
    padding: 0;
}

.color-dot {
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border: 2px solid #e6e6e6;
    cursor: pointer;
    position: relative;
    z-index: 10;
}

.color-dot.selected {
    border-color: #111;
    transform: scale(1.05);
}

.product-description {
    margin: 2rem 0;
}

.product-description p {
    margin: 1rem 0;
    font-size: 1rem;
    font-family: Montserrat, sans-serif !important;
}

.product-footer {
    margin-top: 1rem;
}

.cta-button {
    width: 100%;
    padding: 0.9rem;
    background: black;
    color: white;
    border-radius: 10px;
    cursor: pointer;
    border: none;
    margin-bottom: 4rem;
    text-transform: uppercase;
    font-family: Bruno Ace SC, sans-serif;
}

.product-carousel {
    position: relative;
    width: 100%;
    margin-top: 0.75rem;
    display: flex;
    align-items: center;
}

.product-carousel ul {
    display: flex;
    gap: 0.5rem;
    overflow-x: auto;
    padding: 0.5rem 0;
    margin: 0;
    list-style: none;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
}

.product-carousel li {
    flex: 0 0 auto;
    scroll-snap-align: center;
}

.product-carousel button.thumbnail {
    width: 80px;
    height: 80px;
    border-radius: 8px;
    overflow: hidden;
    display: block;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
    position: relative;
    cursor: pointer;
    background: #fff;
}

.product-carousel button.thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
}

.product-carousel .carousel-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 20;
    font-size: 1.2rem;
}

.product-carousel .carousel-arrow.left {
    left: 0;
}

.product-carousel .carousel-arrow.right {
    right: 0;
}

.product-carousel .carousel-arrow:focus {
    outline: 2px solid #fff;
}

.product-carousel button.thumbnail.selected {
    border: 2px solid #000;
}

