@media (min-width: 768px) {
    .product-page {
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;
        padding: 0 40px;
    }

    .product-page h1 {
        margin: 20px 0;
    }

    .product-main-and-controls {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 20px;
        margin-left: auto;
        margin-right: auto;
    }

    .product-main-image {
        width: 400px;
        height: 400px;
        min-width: 400px;
        min-height: 400px;
        margin: 0;
    }

    .product-main-image img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .product-carousel ul {
        flex: 1;
        width: 100%;
        padding: 0;
        margin: 0;
        list-style: none;
        overflow-y: auto;
        overflow-x: hidden;
        gap: 0.5rem;
        display: flex;
        flex-direction: column;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        max-height: 400px;
    }

    .product-carousel li {
        flex: none;
        scroll-snap-align: center;
    }

    .product-carousel button.thumbnail {
        height: 90px;
        aspect-ratio: 1 / 1;
    }

    .product-carousel button.thumbnail img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .product-carousel {
        position: relative;
        width: 100px;
        height: 500px;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow: visible;
    }

    .product-carousel-wrapper {
        position: relative;
    }

    .product-carousel-wrapper .carousel-arrow.top,
    .product-carousel-wrapper .carousel-arrow.bottom {
        position: relative;
        left: 45%;
        transform: translateX(-50%);
        width: 30px;
        height: 30px;
        background: rgba(0, 0, 0, 0.6);
        color: white;
        border: none;
        border-radius: 50%;
        cursor: pointer;
        z-index: 20;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.2rem;
    }

    .product-carousel-wrapper .carousel-arrow.top {
        top: -5px;
    }

    .product-carousel-wrapper .carousel-arrow.bottom {
        bottom: 85px;
    }

    .product-carousel-wrapper .carousel-arrow {
        display: none;
    }

    .product-carousel-wrapper .carousel-arrow.top,
    .product-carousel-wrapper .carousel-arrow.bottom {
        display: flex;
    }
}